<template>
  <div class="dynamic-share">
    <bottom-download/>
    <div class="user">
      <img :src="user.avatar || $store.state.defaultAvatar" alt="" class="avatar">
      <p>{{ user.nick_name }}</p>
    </div>
    <div class="content">
      <h4 v-if="isUnLock">{{ detail.title }}</h4>
      <div class="img-list" v-if="!detail.video_url && isUnLock">
        <div class="img" v-for="(item, index) in imgs" :key="index" :style="{backgroundImage: `url(${item})`}" />
      </div>
      <div v-if="detail.video_url && isUnLock" id="video_player" :style="{backgroundImage: `url(${detail.image_url})`}">
        <img v-if="showPlayBtn" @click="createPlayer" src="../live-share/img/play@2x.png" alt="">
      </div>
      <a href="/download" class="lock-img" v-if="!isUnLock" :style="{backgroundImage: `url(${blurImgs[0]})`}">
        <p>{{ detail.unlock_price }}金币</p>
        <img src="./nofufei2@3x.png" alt="">
      </a>
      <p class="time">{{ detail.create_time }}</p>
    </div>
    <div class="comment">
      <h4>最新评论（{{ detail.comment_count }}）</h4>
      <div class="msg" :key="item.id" v-for="item in msgs">
        <img :src="item.user.avatar || $store.state.defaultAvatar" alt="" class="avatar">
        <div class="content">
          <span>{{ item.user.nick_name }}</span>
          <p>{{ item.content }}</p>
        </div>
      </div>
    </div>
    <div class="more" @click="loadMore" v-if="showMore">加载更多</div>
  </div>
</template>

<script>
import BottomDownload from '../../components/bottom-download/bottom-download'
import { post } from '../../common/js/http'

export default {
  name: 'dynamic-share',
  components: {
    BottomDownload
  },
  data() {
    return {
      dyId: '',
      user: {},
      detail: {
        image_url: ''
      },
      msgs: [],
      page: 1,
      load: false,
      showMore: false,
      showPlayBtn: true
    }
  },
  created () {
    this.dyId = this.$route.params.id
    this.getDetail()
    this.getComment()
  },
  computed: {
    imgs() {
      return this.detail.image_url.split(',')
    },
    blurImgs() {
      if (this.detail.blur_image_url) {
        return this.detail.blur_image_url.split(',')
      }
      return ''
    },
    isUnLock() {
      if (this.detail.unlock_price === 0) {
        return true
      }
      return this.detail.unlocked && this.detail.unlock_price
    }
  },
  methods: {
    createPlayer() {
      this.player = new window.TcPlayer('video_player', {
        width: '100%',
        height: '100%',
        poster: { src: this.detail.image_url, style: 'cover' },
        // controls: 'none',
        autoplay: true,
        rePlay: true,
        x5_video_position: 'center',
        x5_type: 'h5',
        mp4: this.detail.video_url.indexOf('mp4') >= 0 ? this.detail.video_url : '',
        m3u8: this.detail.video_url.indexOf('m3u8') >= 0 ? this.detail.video_url : ''
      })
      this.showPlayBtn = false
    },
    getDetail() {
      post('/webapi/Moment/getMomentInfo', { id: this.dyId })
        .then(({ data: { data } }) => {
          this.user = data.user
          this.detail = data
        })
    },
    loadMore() {
      this.page++
      this.getComment()
    },
    getComment() {
      if (this.load) return
      this.load = true
      post('/webapi/Moment/getComments', { momentid: this.dyId, page: this.page, size: 5 })
        .then(({ data: { data } }) => {
          this.msgs = this.msgs.concat(data)
          this.load = false
          this.showMore = data.length >= 5
        })
    }
  }
}
</script>

